<template>
    <div id="fotos">
        <div class="grid_fotos">
            <div v-for="i in 6" v-bind:key="i">
                <a @click="redirect(i)" >
                    <p>{{ i }}</p>
                    <img v-bind:src="cargaFoto(i)" alt="">
                </a>
            </div>
            
        </div>
    </div>
</template>
<script>
export default{
  name:'fotos',
  data: function () {
    return{

    }
  },
  methods: {
    cargaFoto: function (index) {
        return `https://picsum.photos/200/300?grayscale/${index}`
    },
    redirect: function (id) {
        this.$router.push(`/fotos/${id}`)
    }

  }
}
</script>
<style scoped>
.grid_fotos{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;

}
</style>