<template>
<div id="peliculas">
    <h1>Este es el padre de las peliculas</h1>
    <div class="container border border-success p-2">
      <div class="row">
        <div class="col-md-12">
          <h1>Bienvenid@ </h1>
          <p>Aquí podrás encontrar información de peliculas de anime de Studio Ghibli</p>
          <button class="btn btn-warning">Limpiar informacion</button>
        </div>
      </div>
    </div>
    <div class="container p-2">
      <div class="row">
        <div class="col-md-12">
          <p>A continuación hay un listado con los nombres de las peliculas</p>
          </div>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md-12">
        <select name="" id="">
            <option ></option>
        </select>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md-12">
            <p>card de la pelicula</p>
            
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md-12">
              <!-- dattos que son enviados del hijo -->
            <movie
              v-on:desdehijo = "atrapadatos" 
            v-bind:terminado="mensaje"
            v-bind:datosPelicula="datosConsumidos"
            ></movie>
            <!-- terminado es un props del hijo -->
        </div>
      </div>
    </div>
    

</div>
</template>
<script>

import movie from '@/components/movie.vue'
 export default{
    name: 'peliculas',
    data: function () {
        return{
            mensaje:'',
            //datos consumidos de la api que esta en el modelo de datos 
            datosConsumidos:[]
          // nombre:''
        }
    },
    components:{
       movie
    },
    methods: {
      atrapadatos: function (flag) {
        this.mensaje ='esperando datos de Api'
       if(flag==true){
        let url = 'https://ghibliapi.vercel.app/films'
        fetch(url)
        .then(respuesta => respuesta.json())//promesa
        .then(datos =>{
          // this.nombre = datos[0].title
          let timeout = 10000
          setTimeout(() => {
            console.log(datos) //por el momento imprime los datos en consola
            this.mensaje = 'Se termino de consumir el API'
            this.datosConsumidos = datos
          }, timeout);
        })
        .catch(error => console.log('error en consumir la api', error))
       }else{
        console.log('error desconocido')
       }
      }
    }
 }
</script>
