import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import PeliculasView from '../views/PeliculasView.vue'
import Fotos from '../views/Fotos.vue'
import Foto from '../views/Foto.vue'
import padrePokemon from "../views/PadrePokemon.vue";
import Pokemon from "../components/Pokemon.vue";
import History from '@/views/History.vue'

const routes = [
  
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path:'/peliculas',
    name:'peliculas',
    component: PeliculasView
  },
  {
    path:'/fotos',
    name:'fotos',
    component: Fotos
  },
  {
    path:'/fotos/:id',
    name:'foto',
    component: Foto
  },
  {
    path:'/padrePokemon',
    name:'padrePokemon',
    component: padrePokemon
    
  },
  {
    path:'/padrePokemon/:id',
    name:'Pokemon',
    component: Pokemon,
    props:true
  },
  {
    path:'/history',
    name:'History',
    component:History,
    props:{
    backStyle:true,
    }
    },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: function () {
      return import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
    }
  },
  
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
