<template>
    <div id="Pokemon">
        <p>hijo pokemon</p>
        <p>{{ id }}</p>
        <p>nombre: {{ name }}</p>
        
        <img :src="image" alt="">
    </div>
</template>
<script>
import axios from 'axios'
export default {
    
    props: ['id'],
    name: 'Pokemon',
    data: function () {
        return {
            name:'',
            image:''
        }
    },
    methods: {
        fetchPokemon() {
            axios.get(`https://pokeapi.co/api/v2/pokemon/${this.id}`)
                .then(resp => {
                    console.log(resp);
                    this.setData(resp.data);
                })
                .catch(error => {
                    console.log(error)
                })
        },
        setData(data) {
            this.name = data.name;
            this.image = data.sprites.front_default;
        }
    },
    created() {
        this.fetchPokemon();
    }

}
</script>
