<template>
  <div id="movie">
    <h3>{{ mensajeEspera }}</h3>   
    <!-- este v-for recorre datos peliculas que es de tipo array que viene desde el padre al hijo atraves de un props que es el array de la api que esta alamcenado en una variable en el modelo de datos que viaja a la fucion set time out luego viaja al props que esta siendo enviado al hijo con un v-bind datos peliculas  -->
    <ul v-for="(peliculas,index)  in datosPelicula" v-bind:key="index">
      <li>{{ peliculas.title }}</li>
      <p></p>
    </ul>
    <button v-on:click="consumo">HasmeClick!</button>
  </div>
</template>

<script>
export default {
  name: 'movie',
  props:{

      terminado:{
        type: String,
        required: true
      },
      datosPelicula:{
        type: Array,
        required: true
      }
  },
  data: function () {
    return{
      esperando:''
    }
  },
  methods:{
    consumo:function () {
      // this.esperando = 'Consumiendo datos espere...'
      this.$emit('desdehijo', true)
    }
  },
  computed: {
    mensajeEspera: function () {
      return this.terminado
    }
  }
  
}
</script>


