<template>
    <div id="padrePokemon">
            <p>padre de pokemon</p>
            <form action="">
                <h1>buscador</h1>
                <label for="txtPokemon">Ingresa el id del pokemon (1-200)</label>
                <input type="number" id="txtPokemon" v-model="pokemon_id">
                <button @click.prevent="search">Buscar</button>

            </form>
    </div>
</template>
<script>
    export default{
        name: 'padrePokemon',
        data: function () {
            return {
                pokemon_id:0
            }
        },
        methods: {
            search: function () {
                if (this.pokemon_id ==='')
                return
                this.$router.push(`/padrePokemon/${this.pokemon_id}`)
            }
        }
    }
</script>
