<template>
    <div id="foto">
        <div v-if="id == 1" class="titulo">
            <p>Foto {{ id }}</p>
        </div>
        <div v-else-if="id == 2" class="titulo bg-info">
            <p>Foto {{ id }}</p>
        </div>
        <div v-else-if="id == 3" class="titulo bg-success">
            <p>Foto {{ id }}</p>
        </div>
        <div v-else class="titulo2 bg-success">
            <p>Foto {{ id }}</p>
        </div>
        <p>jjj</p>


        <img v-bind:src="src" alt="">
        <button class="btn btn-warning" @click="regrsar">Regresarr</button>


    </div>
</template>
<script>
export default {
    name: 'foto',

    data: function () {
        return {
            src: '',
            ruta: '/fotos'
        }
    },
    methods: {
        regrsar: function () {
            return this.$router.push(this.ruta)

        }
    },
    //propiedad computada
    computed: {
        id() {
            return this.$route.params.id
        }
    },
    // hook
    created() {
        this.src = `https://picsum.photos/200/300?grayscale/${this.id}`
    }

}
</script>
<style scoped> .titulo2 {
     display: flex;
     flex-direction: column;
     justify-content: center;
     background-color: black;
     color: white;
     font-size: medium;


 }

 .titulo {
     display: flex;
     flex-direction: column;
     justify-content: center;
     color: white;
     font-size: medium;


 }

 #foto {
     display: flex;
     flex-direction: row;
     justify-content: center;
 }

 img {
     width: 20%;
     height: auto;
 }
</style>